.main-banner{
    width:1700px;
    max-width: 100%;
    background-image: url(/img/main-banner.jpg);
    background-repeat: no-repeat;
    background-position: center;
    margin: 0 auto;
    background-size: cover;
    &__container{
        padding: 136px 140px 59px 140px;
    }
    &__header{
        font-size: 60px;
        line-height: 60px;
        margin-bottom: 35px;
    }
    &__text{   
    font-size: 18px;
    }
    &__link{
        font-size: 18px;
        font-weight: bold;
        padding-left: 28px;
        margin-top: 142px;
        display: flex;
        position: relative;
        align-items: center;
    }
    &__link:before{
        content:"";
        background-image: url(/img/sprites/sprite.svg#arrow_down);
        height: 46px;
        width: 30px;
        position: absolute;
        left: -6px;
        background-position: center;
        background-repeat: no-repeat;
    }
    &__link:hover{
        text-decoration: none;
    }
}

@media(max-width:1600px){
    .main-banner__container{
        padding-left: 248px;
    }

}
@media(max-width:1280px){
    .main-banner__container {
        padding-left: 20px;
        padding-top: 101px;
        padding-right: 20px;
        padding-bottom: 71px;
    }
    .main-banner__header{
        line-height: 40px;
        margin-bottom: 41px;
    }
    .main-banner__link{
        margin-top: 118px;
    }
}
