.main-catalog{
    &__container{
        padding-top: 73px;
        padding-bottom: 90px;
    }
    &__list{
        max-width: 1420px;
        justify-content: space-between;
        margin: 0 auto;
    }
    &__list-item{
        width: 460px;
        height: 300px;
        max-width: 100%;
        position: relative;
    }
    &__item-btn{
        position: absolute;
        right: 30px;
        bottom: 28px;
        padding: 19px 42px;
    }
    &__item-img{
        object-fit: cover;
        height: 100%;
    }
}

@media(max-width:1600px){
    .main-catalog__list-item{
        width: 400px;
    height: 300px;
    }
    .main-catalog__container{
        padding-bottom: 82px;
    }
}
@media(max-width:1280px){
    .main-catalog__container{
        padding-top: 54px;
        padding-bottom: 65px;
    }
    .main-catalog__list-item{
        width: 300px;
    }
    .main-catalog__item-btn{
        right: 12px;
        bottom: 18px;
    }
}
@media(max-width:960px){
    .main-catalog__list{
        flex-direction: column;
        max-width: 460px;
        margin: 0 auto;
    }
    .main-catalog__list-item{
        width: 460px;
        margin-bottom: 20px;
    }
}
