html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
    outline: none;
  }
  html { height: 101%; } /* always display scrollbars */
  body { font-size: 62.5%; line-height: 1; font-family: Circe, Arial, Tahoma, Verdana, sans-serif; color:#21303d; }
  
  article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section { display: block; }
  ol, ul { list-style: none; }
  
  blockquote, q { quotes: none; }
  blockquote:before, blockquote:after, q:before, q:after { content: ''; content: none; }
  strong { font-weight: bold; } 
  
  input { outline: none; }
  
  table { border-collapse: collapse; border-spacing: 0; }
  img { border: 0; max-width: 100%; }
  
  a { text-decoration: none; color: #21303d;}
  
  a:hover { text-decoration: underline; }
  img{
      object-fit: cover;
  }
  button{
      cursor:pointer;
  }
.show {
    display: block;
}
.hide {
    display: none!important;
}

.d-flex{
    display:flex;
}
ul{
    list-style:none;
}
.container {
    max-width: 1580px;
    margin: 0 auto;
    padding-left: 20px;
    padding-right: 20px;
}

@media screen and (max-width: 1600px)
{.container {
    max-width: 1260px;
}}
@media screen and (max-width: 1280px)
{.container {
    max-width: 940px;
}}


.bg_blue{
    background-color: #21303d;
}
.bg_pink{
    background-color: #e5b3af;
}
.bg_violent{
    background-color: #aca8c3;
}
.clr_blue{
    color: #21303d;
}
.clr_pink{
    color: #e5b3af;
}
.clr_violent{
    color: #aca8c3;
}
.h1{
    font-weight: 700;
    font-size: 50px;
    line-height: 32px;
}
.h2{
    font-weight: 700;
    font-size: 30px;
    line-height: 32px;
}
.h3{
    font-size: 18px;
    font-weight: 700;
    line-height: 30px;
}
@media(max-width:1280px){
    .h1{
        font-size: 40px;
    }
    .h2{
        font-size: 26px;
    }
}
.btn{
    font-size:11px;
    text-transform: uppercase;
    font-weight: bold;
    color:white;
}
.btn:hover{
    text-decoration: none;
}
.btn.bg_blue{
    color:white;
}
.btn.bg_pink{
    color:#21303d;
}
.btn.bg_blue:hover{
    background-color: #384551;
}
.btn.bg_pink:hover{
    background-color: #f3c8cd;
}
@media(max-width:768px){
    .h1{
        font-size: 28px;
    }
    .h2{
        font-size: 18px;
    }
}
.p{
    font-size: 16px;
    font-weight: 300;
    line-height: 32px;
}


